@media (max-width: 768px) {
  body header .header-top .hamburguer-btn {
    position: absolute;
    top: 28px;
    opacity: 0.8;
  }
  body header .header-top .hamburguer-btn .btn {
    position: absolute;
    top: 0;
    left: -14px;
    cursor: pointer;
  }
  body header .header-top .hamburguer-btn .active .btn-left {
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background: transparent;
  }
  body header .header-top .hamburguer-btn .active .btn-left:before {
    -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
    transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
  }
  body header .header-top .hamburguer-btn .active .btn-left:after {
    -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
    transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
  }
  body header .header-top .hamburguer-btn .btn-left {
    background-color: #4f4f4f;
    position: absolute;
    height: 8px;
    width: 15px;
    top: 30px;
    left: 15px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  body header .header-top .hamburguer-btn .btn-left:before {
    background-color: #4f4f4f;
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: -12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  body header .header-top .hamburguer-btn .btn-left:after {
    background-color: #4f4f4f;
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: 12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  body header .header-top .hamburguer-btn .btn-right {
    background-color: #4f4f4f;
    position: absolute;
    height: 8px;
    width: 15px;
    top: 30px;
    left: 30px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  body header .header-top .hamburguer-btn .btn-right:before {
    background-color: #4f4f4f;
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: -12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  body header .header-top .hamburguer-btn .btn-right:after {
    background-color: #4f4f4f;
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: 12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  body header .header-top .hamburguer-btn .active .btn-left {
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background: transparent;
  }
  body header .header-top .hamburguer-btn .active .btn-left:before {
    -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
    transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
  }
  body header .header-top .hamburguer-btn .active .btn-left:after {
    -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
    transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
  }
  body header .header-top .hamburguer-btn .active .btn-right {
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background: transparent;
  }
  body header .header-top .hamburguer-btn .active .btn-right:before {
    -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(-4px, 4px);
    transform: rotateZ(-45deg) scaleX(1.4) translate(-4px, 4px);
  }
  body header .header-top .hamburguer-btn .active .btn-right:after {
    -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
    transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
  }
}
